import React, {Fragment, useState} from 'react';
import Input from "./Input";
import TitleSection from "./TitleSection";
import ExplanationSection from "./ExplanationSection";
import {connect} from 'react-redux';
import {addClientProfile} from '../../redux/actions/index'


const Profileuser = ({clientProfile}) => {

    const [errorEnvio, getErrorEnvio] = useState('');
    const [successfullSave, getSuccessfullSave] = useState('');
    const [valuesProfile, getValuesProfile] = useState({
        telefono: clientProfile.telefono,
        nombre: clientProfile.nombre,
        direccion: clientProfile.direccion,
        cp: clientProfile.cp,
        poblacion: clientProfile.poblacion,
        email: clientProfile.email,

    })
    const [errorsProfile, getErrorsProfile] = useState({
        telefono: '',
        nombre: '',
        direccion: '',
        cp: '',
        poblacion: '',
        email: '',

    })

    const handleChange = (e) => {
        e.preventDefault();

        const target = e.target;
        const name = target.name;
        const value = target.value;

        switch (name) {
            case 'telefono':
                if (value === '') {
                    getErrorsProfile({
                        ...errorsProfile,
                        "telefono": "El campo es requerido"
                    })
                    return;
                }
                if (!/[0-9]/.test(value)) {
                    getErrorsProfile({
                        ...errorsProfile,
                        "telefono": "Slo se admiten nmeros"
                    })
                    return;
                }
                if (value.length < 9 && value !== '') {
                    getErrorsProfile({
                        ...errorsProfile,
                        "telefono": "Tiene que ser mayor de 9 dgitos"
                    })
                    return;
                }
                getErrorsProfile({
                    ...errorsProfile,
                    "telefono": "",
                })
                break;
            case 'nombre':
                if (value === '') {
                    getErrorsProfile({
                        ...errorsProfile,
                        "nombre": "El campo es requerido"
                    })
                    return;
                }
                if (/[0-9]/.test(value)) {
                    getErrorsProfile({
                        ...errorsProfile,
                        "nombre": "No se admiten nmeros en nombre"
                    })
                    return;
                }
                if (value.length < 3) {
                    getErrorsProfile({
                        ...errorsProfile,
                        "nombre": "Mnimo tres letras para nombre",
                    })
                    return;
                }
                if (!/[a-zA-Z]/.test(value)) {
                    getErrorsProfile({
                        ...errorsProfile,
                        "nombre": "Slo se admite texto",
                    })
                    return;
                }
                getErrorsProfile({
                    ...errorsProfile,
                    "nombre": "",
                })
                break;
            case 'direccion':
                if (value === '') {
                    getErrorsProfile({
                        ...errorsProfile,
                        "direccion": "El campo es requerido"
                    })
                    return;
                }
                if (!/[A-Za-z0-9]/.test(value)) {
                    getErrorsProfile({
                        ...errorsProfile,
                        "direccion": "Slo texto y nmeros"
                    })
                    return;
                }
                if (value.length < 5) {
                    getErrorsProfile({
                        ...errorsProfile,
                        "direccion": "Mnimo tres letras",
                    })
                    return;
                }
                getErrorsProfile({
                    ...errorsProfile,
                    "direccion": "",
                })
                break;
            case 'cp':
                if (value.length < 5 && value !== '') {
                    getErrorsProfile({
                        ...errorsProfile,
                        "cp": "Cdigo Postal debe contener 5 dgitos ",
                    })
                    return;
                }
                if (!/[0-9]/.test(value) && value !== '') {
                    getErrorsProfile({
                        ...errorsProfile,
                        "cp": "Slo se admiten nmeros ",
                    })
                    return;
                }
                getErrorsProfile({
                    ...errorsProfile,
                    "cp": "",
                })
                break;
            case 'poblacion':
                if (!/[a-zA-Z]/.test(value) && value !== '') {
                    getErrorsProfile({
                        ...errorsProfile,
                        "direccion": "Slo se admite texto",
                    })
                    return;
                }
                if (value.length < 2 && value !== '') {
                    getErrorsProfile({
                        ...errorsProfile,
                        "poblacion": "No es una direccin vlida",
                    })
                    return;
                }
                getErrorsProfile({
                    ...errorsProfile,
                    "poblacion": "",
                })
                break;
            case 'email':
                if (!value.includes('@') && value !== '') {
                    getErrorsProfile({
                        ...errorsProfile,
                        "email": "Email incorrecto",
                    })
                    return;
                }
                if (!value.includes('.') && value !== '') {
                    getErrorsProfile({
                        ...errorsProfile,
                        "email": "Email incorrecto",
                    })
                    return;
                }
                getErrorsProfile({
                    ...errorsProfile,
                    "email": "",
                })
                break;
            default:
                break;
        }
        getValuesProfile({
            ...valuesProfile,
            [name]: value
        })
    }

    const dataUser = () => {
        if (valuesProfile.telefono.length > 0 &&
            valuesProfile.nombre.length > 0 &&
            valuesProfile.direccion.length > 0) {
            getErrorEnvio('')
            addClientProfile(valuesProfile)
            getSuccessfullSave('Guardado con xito')
            setTimeout(function () {
                getSuccessfullSave('');
            }, 2000)
        } else {
            getErrorEnvio('Error!! Revise campos requeridos')
        }
    }

    return (
        <Fragment>
            <TitleSection title={'Datos de envo'}/>
            <ExplanationSection
                explanation={'Aqu podrs actualizar cualquier dato relacionado con los envos y contacto.' +
                ' Los datos no sern enviados hasta que realices tu primer pedido.'}
            />
            <div id="userprofile">
                <Input
                    defaultValue={clientProfile.telefono}
                    pattern="[0-9]"
                    settype="number"
                    setid="telefono"
                    setplaceholder="telfono"
                    setname="telefono"
                    icontype={'telefono'}
                    textlabel={'nmero de telfono'}
                    bgIcon={'#B1D8E2'}
                    coloricon={'#707070'}
                    required={true}
                    handleChange={handleChange}
                    errorsProfile={errorsProfile}
                />
                <Input
                    defaultValue={clientProfile.nombre}
                    pattern="[a-zA-Z]"
                    settype="text"
                    setid="nombre"
                    setplaceholder="nombre"
                    setname="nombre"
                    icontype={'nombre'}
                    textlabel={'nombre de usuario'}
                    bgIcon={'#B1D8E2'}
                    coloricon={'#707070'}
                    required={true}
                    handleChange={handleChange}
                    errorsProfile={errorsProfile}
                />
                <Input
                    defaultValue={clientProfile.direccion}
                    pattern="[a-zA-Z]"
                    settype="text"
                    setid="direccion"
                    setplaceholder="direccin de entrega"
                    setname="direccion"
                    icontype={'direccion'}
                    textlabel={'direccin de envo'}
                    bgIcon={'#B1D8E2'}
                    coloricon={'#707070'}
                    required={true}
                    handleChange={handleChange}
                    errorsProfile={errorsProfile}
                />
                <Input
                    defaultValue={clientProfile.cp}
                    settype="number"
                    setid="cp"
                    setplaceholder="cdigo postal"
                    setname="cp"
                    icontype={'cpostal'}
                    textlabel={'Cdigo postal'}
                    bgIcon={'#B1D8E2'}
                    coloricon={'#707070'}
                    required={false}
                    handleChange={handleChange}
                    errorsProfile={errorsProfile}
                />
                <Input
                    defaultValue={clientProfile.poblacion}
                    settype="text"
                    setid="poblacion"
                    setplaceholder="poblacin"
                    setname="poblacion"
                    icontype={'poblacion'}
                    textlabel={'Poblacin'}
                    bgIcon={'#B1D8E2'}
                    coloricon={'#707070'}
                    required={false}
                    handleChange={handleChange}
                    errorsProfile={errorsProfile}
                />
                <Input
                    defaultValue={clientProfile.email}
                    settype="email"
                    setid="email"
                    setplaceholder="correo electrnico"
                    setname="email"
                    icontype={'email'}
                    textlabel={'correo electrnico'}
                    bgIcon={'#B1D8E2'}
                    coloricon={'#707070'}
                    required={false}
                    handleChange={handleChange}
                    errorsProfile={errorsProfile}
                />
                {errorEnvio !== '' ?
                    <p style={{textAlign: 'center', color: 'red', textDecoration: 'underline'}}>
                        {errorEnvio}
                    </p>
                    :
                    null
                }
                {successfullSave !== '' ?
                    <p style={{textAlign: 'center', color: 'green', textDecoration: 'underline'}}>
                        {successfullSave}
                    </p>
                    :
                    null
                }
                <button id="submit_login"
                        onClick={dataUser}
                >Guardar
                </button>
            </div>
        </Fragment>
    )
}

function mapStateToProps(state) {
    return {
        clientProfile: state.ClientProfile.clientProfile
    }
}

export default connect(mapStateToProps)(Profileuser);